import { PropertyConfigGroup } from "./PropertyConfigGroup";

/**
 * 属性分组
 */
export class PropertyConfigTab {
    /**
     * 分组ID
     */
    tabId: string;
    /**
     * 分组名
     */
    tabName: string;
    /**
     * 属性分组
     */
    groups: PropertyConfigGroup[] = [];

    constructor(tabId?: string, tabName?: string) {
        this.tabId = tabId;
        this.tabName = tabName;
    }
}